import React from 'react';
import { Route as OrdinaryRoute } from 'react-router-dom';
import MainLayout from 'layouts/MainLayout';
import { EnhancedRouteProps } from 'types/common';

const EnhancedRoute: React.FC<EnhancedRouteProps> = (props) => {
  const {
    layout: Layout,
    component: Component,
    ...restProps
  } = props;

  if (Component) {
    const renderRoute = (componentProps) => (
      Layout
        ? (
          <Layout {...componentProps}>
            <Component {...componentProps} />
          </Layout>
        )
        : (
          <MainLayout>
            <Component {...componentProps} />
          </MainLayout>
        )
    );

    return (
      <OrdinaryRoute {...restProps} render={renderRoute} />
    );
  }

  return null;
};

export default EnhancedRoute;
