import { AccountActionTypes } from 'types/accountActionTypes';
import AccountState from 'types/accountType';
import {
  GET_ACCOUNT_PENDING,
  GET_REGION_SUCCESS,
  GET_USER_SUCCESS,
  ACCOUNT_STOP_LOADING,
  GET_TEACHER_SUCCESS,
  GET_TRANSACTIONS_SUCCESS,
  GET_HEALTH_HISTORY_SUCCESS,
  GET_TIMEZONES_SUCCESS,
  CHANGE_USER_INFO_PENDING,
  CHANGE_USER_INFO_SUCCESS,
  RESET_ACCOUNT,
} from './actionTypes';

const initialState: AccountState = {
  userInfo: null,
  userLoading: false,
  teacherInfo: null,
  teacherLoading: false,
  balanceHistory: [],
  balanceHistoryLoading: false,
  healthHistory: [],
  healthHistoryLoading: false,
  timezones: [],
  timezoneLoading: false,
  regionInfo: null,
  regionLoading: false,
  topLoading: true,
  changeUserInfoPending: false,
};

export default (
  state = initialState,
  action: AccountActionTypes,
): AccountState => {
  switch (action.type) {
    case GET_ACCOUNT_PENDING:
      return {
        ...state,
        userLoading: true,
        teacherLoading: true,
        regionLoading: true,
        healthHistoryLoading: true,
        balanceHistoryLoading: true,
        timezoneLoading: true,
        topLoading: false,
      };
    case ACCOUNT_STOP_LOADING:
      return {
        ...state,
        userLoading: false,
        teacherLoading: false,
        regionLoading: false,
        healthHistoryLoading: false,
        balanceHistoryLoading: false,
        topLoading: false,
      };
    case GET_USER_SUCCESS:
      return {
        ...state,
        userInfo: action.payload.userInfo,
        userLoading: false,
      };
    case GET_REGION_SUCCESS:
      return {
        ...state,
        regionInfo: action.payload.regionInfo,
        regionLoading: false,
      };
    case GET_TEACHER_SUCCESS:
      return {
        ...state,
        teacherInfo: action.payload.teacherInfo,
        teacherLoading: false,
      };
    case GET_TRANSACTIONS_SUCCESS:
      return {
        ...state,
        balanceHistory: [
          ...state.balanceHistory,
          ...action.payload.transactions,
        ],
        balanceHistoryLoading: false,
      };
    case GET_HEALTH_HISTORY_SUCCESS:
      return {
        ...state,
        healthHistory: [
          ...state.healthHistory,
          ...action.payload.healthHistory,
        ],
        healthHistoryLoading: false,
      };
    case GET_TIMEZONES_SUCCESS:
      return {
        ...state,
        timezones: action.payload.timezones,
        timezoneLoading: false,
      };
    case CHANGE_USER_INFO_PENDING:
      return {
        ...state,
        changeUserInfoPending: true,
      };
    case CHANGE_USER_INFO_SUCCESS:
      return {
        ...state,
        changeUserInfoPending: false,
      };
    case RESET_ACCOUNT:
      return initialState;
    default: return state;
  }
};
