import {
  call,
  takeEvery,
  put,
} from 'typed-redux-saga';

import {
  fetchTeacherInfo,
} from 'utils/request';
import { GetTeacherRequestActionType } from 'types/teacherActionTypes';

import {
  GET_TEACHER_REQUEST,
} from './actionTypes';

import {
  teacherSuccess,
} from './actions';

function* loadTeacherInfo({ payload: teacherId }: GetTeacherRequestActionType) {
  try {
    const { data: teacher } = yield* call(fetchTeacherInfo, teacherId);

    yield* put(teacherSuccess(teacher));
  } catch (e) {
    // console.log(e);
  }
}

function* teacherSaga() {
  yield* takeEvery(GET_TEACHER_REQUEST, loadTeacherInfo);
}

export default [
  teacherSaga(),
];
