import {
  call,
  takeEvery,
  put,
} from 'typed-redux-saga';
import {
  OperateSlotsActionType,
  SchedulePendingActionType,
} from 'types/scheduleActionTypes';

import {
  fetchTeacherClasses,
  fetchWeeklySlots,
  fetchClassesHistory,
  teacherOperations,
} from 'utils/request';

import {
  stopLoading,
  weeklySlotsSuccess,
  classesSuccess,
  historyClassesSuccess,
  operateSlotsSuccess,
} from './actions';

import {
  GET_SCHEDULE_PENDING,
  OPERATE_SLOTS_PENDING,
} from './actionTypes';

function* loadWeeklySlots() {
  try {
    const { data } = yield* call(fetchWeeklySlots);

    yield* put(weeklySlotsSuccess(data._embedded.slots));
  } catch (e) {
    yield* put(stopLoading());
  }
}

function* WeeklySlotsSaga() {
  yield* takeEvery(GET_SCHEDULE_PENDING, loadWeeklySlots);
}

function* loadTeacherClasses({ payload }: SchedulePendingActionType) {
  try {
    const { data } = yield* call(fetchTeacherClasses, payload.teacherClassesParams);

    yield* put(classesSuccess(data._embedded.classes));
  } catch (e) {
    yield* put(stopLoading());
  }
}

function* ClassesSaga() {
  yield* takeEvery(GET_SCHEDULE_PENDING, loadTeacherClasses);
}

function* loadHistoryClasses({ payload }: SchedulePendingActionType) {
  try {
    const { data } = yield* call(fetchClassesHistory, payload.historyClassesParams);

    yield* put(historyClassesSuccess(data._embedded.records));
  } catch (e) {
    yield* put(stopLoading());
  }
}

function* HistoryClassesSaga() {
  yield* takeEvery(GET_SCHEDULE_PENDING, loadHistoryClasses);
}

function* operateSlots({ payload }: OperateSlotsActionType) {
  try {
    let success = false;

    if (payload.start_time?.constructor === Array && payload.action !== 'delete') {
      for (const time of payload.start_time) {
        const { data } = yield* call(teacherOperations, { ...payload, start_time: time });

        if (data.result === 'ok') {
          success = true;
        }
      }
    } else {
      const { data } = yield* call(teacherOperations, payload);

      if (data.result === 'ok') {
        success = true;
      }
    }

    if (success) {
      yield* put(operateSlotsSuccess());
    }
  } catch (e) {
    // console.log(e);
  }
}

function* operateSlotsSaga() {
  yield* takeEvery(OPERATE_SLOTS_PENDING, operateSlots);
}

export default [
  WeeklySlotsSaga(),
  ClassesSaga(),
  HistoryClassesSaga(),
  operateSlotsSaga(),
];
