import React, {
  FC,
  ReactNode,
  ButtonHTMLAttributes,
  memo,
} from 'react';
import classnames from 'classnames/bind';
import { Link } from 'react-router-dom';

import styles from './styles.module.scss';

const cx = classnames.bind(styles);

interface PropsType extends ButtonHTMLAttributes<HTMLButtonElement> {
  theme?: 'primary' | 'link' | '';
  href?: string;
  className?: string;
  text?: string;
  children?: ReactNode;
  type?: 'button' | 'submit';
}

const Button: FC<PropsType> = (props) => {
  const {
    theme = '',
    href,
    className = '',
    text,
    type = 'button',
    children,
    ...restProps
  } = props;

  const Component = (
    <button
      className={cx('button', {
        [className]: !!className,
        [theme]: !!theme,
      })}
      type={type}
      {...restProps}
    >
      {text || children}
    </button>
  );

  return href ? (
    <Link to={href}>{Component}</Link>
  ) : (
    Component
  );
};

export default memo(Button);
