import { LibraryActionTypes } from 'types/libraryActionTypes';
import LibraryState from 'types/libraryType';
import {
  GET_LIBRARY_LINKS_SUCCESS,
  GET_LIBRARY_PENDING,
  GET_LIBRARY_SECTIONS_SUCCESS,
  LIBRARY_STOP_LOADING,
  RESET_LIBRARY,
} from './actionTypes';

const initialState: LibraryState = {
  sections: [],
  links: [],
  sectionLoading: false,
  linkLoading: false,
  topLoading: true,
};

export default (
  state = initialState,
  action: LibraryActionTypes,
): LibraryState => {
  switch (action.type) {
    case GET_LIBRARY_PENDING:
      return {
        ...state,
        sectionLoading: true,
        linkLoading: true,
        topLoading: false,
      };
    case LIBRARY_STOP_LOADING:
      return {
        ...state,
        sectionLoading: false,
        linkLoading: false,
        topLoading: false,
      };
    case GET_LIBRARY_SECTIONS_SUCCESS:
      return {
        ...state,
        sectionLoading: false,
        sections: action.payload.sections,
      };
    case GET_LIBRARY_LINKS_SUCCESS:
      return {
        ...state,
        linkLoading: false,
        links: action.payload.links,
      };
    case RESET_LIBRARY:
      return initialState;
    default: return state;
  }
};
