import { DashboardActionTypes } from 'types/dashboardActionTypes';
import DashboardState from 'types/dashboardType';
import {
  GET_UPCOMING_CLASS_CURRICULUM_PENDING,
  GET_UPCOMING_CLASS_FEEDBACK_PENDING,
  GET_MORE_COMPLETED_CLASSES_PENDING,
  GET_NEXT_CLASS_CURRICULUM_PENDING,
  GET_NEXT_CLASS_FEEDBACK_PENDING,
  GET_DASHBOARD_PENDING,

  GET_UPCOMING_CLASS_CURRICULUM_SUCCESS,
  GET_UPCOMING_CLASS_FEEDBACK_SUCCESS,
  GET_MORE_COMPLETED_CLASSES_SUCCESS,
  GET_NEXT_CLASS_CURRICULUM_SUCCESS,
  GET_NEXT_CLASS_FEEDBACK_SUCCESS,
  GET_COMPLETED_CLASSES_SUCCESS,
  GET_UPCOMING_CLASSES_SUCCESS,
  GET_ESTIMATION_TAGS_SUCCESS,
  GET_STATISTICS_SUCCESS,
  GET_TEACHER_SUCCESS,
  GET_REGION_SUCCESS,
  GET_USER_SUCCESS,

  DASHBOARD_STOP_LOADING,
  RESET_DASHBOARD,
} from './actionTypes';

const initialState: DashboardState = {
  profileInformation: {},
  userLoading: false,
  teacherLoading: false,
  regionLoading: false,

  statistics: {},
  feedback: {},
  tags: {},
  statisticsLoading: false,
  tagsLoading: false,

  nextClass: null,
  nextClassCurriculumLoading: false,
  nextClassFeedbackLoading: false,

  upcomingClasses: [],
  upcomingClassesLoading: false,

  completedClasses: [],
  nextCompletedClassesLink: null,
  completedClassesLoading: false,

  topLoading: false,
};

export default (
  state = initialState,
  action: DashboardActionTypes,
): DashboardState => {
  switch (action.type) {
    case GET_DASHBOARD_PENDING:
      return {
        ...state,
        userLoading: true,
        teacherLoading: true,
        regionLoading: true,
        statisticsLoading: true,
        tagsLoading: true,
        upcomingClassesLoading: true,
        completedClassesLoading: true,
      };
    case DASHBOARD_STOP_LOADING:
      return {
        ...state,
        userLoading: false,
        teacherLoading: false,
        regionLoading: false,
        statisticsLoading: false,
        tagsLoading: false,
        upcomingClassesLoading: false,
        completedClassesLoading: false,
        topLoading: false,
      };
    case GET_USER_SUCCESS:
      return {
        ...state,
        profileInformation: {
          ...state.profileInformation,
          ...action.payload.profileInformation,
        },
        userLoading: false,
      };
    case GET_TEACHER_SUCCESS:
      return {
        ...state,
        profileInformation: {
          ...state.profileInformation,
          ...action.payload.profileInformation,
        },
        teacherLoading: false,
      };
    case GET_REGION_SUCCESS:
      return {
        ...state,
        profileInformation: {
          ...state.profileInformation,
          ...action.payload.profileInformation,
        },
        regionLoading: false,
      };
    case GET_STATISTICS_SUCCESS:
      return {
        ...state,
        statistics: {
          ...action.payload.statistics,
        },
        feedback: {
          ...action.payload.feedback,
        },
        statisticsLoading: false,
      };
    case GET_ESTIMATION_TAGS_SUCCESS:
      return {
        ...state,
        tags: action.payload.tags,
        tagsLoading: false,
      };
    case GET_UPCOMING_CLASSES_SUCCESS:
      return {
        ...state,
        upcomingClasses: action.payload.upcomingClasses || [],
        nextClass: action.payload.nextClass,
        upcomingClassesLoading: false,
      };
    case GET_COMPLETED_CLASSES_SUCCESS:
      return {
        ...state,
        completedClasses: action.payload.completedClasses,
        completedClassesLoading: false,
        nextCompletedClassesLink: action.payload.nextCompletedClassesLink,
      };
    case GET_MORE_COMPLETED_CLASSES_PENDING:
    case GET_UPCOMING_CLASS_CURRICULUM_PENDING:
    case GET_UPCOMING_CLASS_FEEDBACK_PENDING:
      return {
        ...state,
        topLoading: true,
      };
    case GET_MORE_COMPLETED_CLASSES_SUCCESS:
      return {
        ...state,
        completedClasses: [
          ...state.completedClasses,
          ...action.payload.completedClasses,
        ],
        nextCompletedClassesLink: action.payload.nextCompletedClassesLink,
        topLoading: false,
      };
    case GET_NEXT_CLASS_CURRICULUM_PENDING:
      return {
        ...state,
        nextClassCurriculumLoading: true,
      };
    case GET_NEXT_CLASS_FEEDBACK_PENDING:
      return {
        ...state,
        nextClassFeedbackLoading: true,
      };
    case GET_NEXT_CLASS_CURRICULUM_SUCCESS:
      return {
        ...state,
        // nextClass: {
        //   ...state.nextClass,
        //   lessonTitle: action.payload.lessonTitle,
        // },
        nextClassCurriculumLoading: false,
      };
    case GET_UPCOMING_CLASS_CURRICULUM_SUCCESS: {
      const { lessonTitle, classId } = action.payload;
      const newUpcomingClasses = state.upcomingClasses.map((upcomingClass) => ({
        ...upcomingClass,
        lessonTitle: upcomingClass.id === classId ? lessonTitle : upcomingClass.lessonTitle,
      }));

      return {
        ...state,
        upcomingClasses: newUpcomingClasses,
        topLoading: false,
      };
    }
    case GET_UPCOMING_CLASS_FEEDBACK_SUCCESS: {
      const { classId, feedback } = action.payload;
      const newUpcomingClasses = state.upcomingClasses.map((upcomingClass) => ({
        ...upcomingClass,
        feedback: upcomingClass.id === classId ? feedback : upcomingClass.feedback,
      }));

      return {
        ...state,
        upcomingClasses: newUpcomingClasses,
        topLoading: false,
      };
    }
    case GET_NEXT_CLASS_FEEDBACK_SUCCESS: {
      return {
        ...state,
        // nextClass: {
        //   ...state.nextClass,
        //   feedback: action.payload.nextClassFeedback,
        // },
        nextClassFeedbackLoading: false,
      };
    }
    case RESET_DASHBOARD:
      return initialState;
    default: return state;
  }
};
