import differenceInYears from 'date-fns/differenceInYears';
import format from 'date-fns/format';
import { utcToZonedTime } from 'date-fns-tz';
import intervalToDuration from 'date-fns/intervalToDuration';
import differenceInMilliseconds from 'date-fns/differenceInMilliseconds';

import { classStatus } from 'constants/class';
import {
  ClassType,
  NormalizedCompletedClassType,
} from 'types/common';
import { NextClassType, UpcomingClassType } from 'types/dashboardType';
import paths from 'routes/paths';
import { TOKEN_NAME, USER_ID_KEY } from 'constants/common';
import history from 'utils/history';

export const normalizeCompletedClass = (
  completedClass: ClassType,
): NormalizedCompletedClassType => {
  const {
    id,
    duration,
    start_time,
    source_of_fail,
    reason_of_fail,
    result,
    _embedded: {
      student,
      teacher,
      lesson,
      transactions_amount,
    },
  } = completedClass;

  let studentInfo = '';

  if (student) {
    const {
      name,
      id: studentId,
      region_code,
      gender,
      birth_date,
      birth_year,
    } = student;

    const birthDate = birth_date ? new Date(birth_date) : new Date(birth_year, 0, 0);
    const age = differenceInYears(new Date(), birthDate);
    studentInfo = `${name}, ${age} y.o., #${studentId}${region_code}, ${gender || ''}`;
  }

  let title = '';

  if (teacher) {
    const { timezone = '' } = teacher;

    const time = format(utcToZonedTime(start_time, timezone), 'MM/dd/yy h:mm a O');
    title = `${time} (${timezone}), ${duration} min`;
  }

  const lessonTitle = lesson.full_name;
  const credit = transactions_amount.value;

  const status = (
    result === 'completed'
      ? classStatus.completed as string
      : classStatus[source_of_fail]?.[reason_of_fail] as string
  );

  const replayDataLink = (
    result === 'completed' && completedClass?._links?.replay_data
      ? `/view/${id}`
      : undefined
  );

  return {
    studentInfo,
    title,
    lessonTitle,
    credit,
    status,
    id,
    replayDataLink,
  };
};

export const normalizeUpcomingClass = (
  upcomingClass: ClassType,
): UpcomingClassType => {
  const {
    id,
    duration,
    start_time,
    lesson_id,
    _embedded: {
      student,
      lesson,
      teacher,
    },
  } = upcomingClass;

  let lessonId = lesson_id;
  let studentInfo = '';
  let title = '';

  if (student) {
    const {
      name,
      id: studentId,
      region_code,
      gender,
      birth_date,
      birth_year,
      actual_next_lesson_id,
    } = student;

    const birthDate = birth_date ? new Date(birth_date) : new Date(birth_year, 0, 0);
    const age = differenceInYears(new Date(), birthDate);
    studentInfo = `${name}, ${age} y.o., #${studentId}${region_code}, ${gender || ''}`;

    if (!lessonId) {
      lessonId = actual_next_lesson_id;
    }
  }

  if (teacher) {
    const { timezone = '' } = teacher;
    const time = format(utcToZonedTime(start_time, timezone), 'MM/dd/yy h:mm a O');
    title = `${time} (${timezone}), ${duration} min`;
  }

  const lessonTitle = lesson.full_name;
  const enterLink = `/classroom/${id}/`;

  return {
    enterLink,
    studentInfo,
    lessonTitle,
    title,
    id,
    lessonId,
  };
};

export const normalizeNextClass = (
  nextClass: ClassType,
): NextClassType | null => {
  if (!nextClass) return null;

  const normalizedNextClass = normalizeUpcomingClass(nextClass);

  return {
    ...normalizedNextClass,
    classStartTime: nextClass.start_time,
  };
};

export const getNextClass = (upcomingClasses: ClassType[]): ClassType | null => {
  let nextClass: ClassType | null = null;
  let minDelta: number | null = null;

  for (const item of upcomingClasses) {
    const now = new Date().getTime();
    const start_time = new Date(item.start_time).getTime();

    if (
      item.status !== 'done'
      && (!minDelta || minDelta > start_time - now)
    ) {
      nextClass = item;
      minDelta = start_time - now;
    }
  }

  return nextClass;
};

export const goToLogin = (): void => {
  if (window.location.pathname !== paths.signin) {
    history.push(paths.signin);
  }
};

export const logout = (): void => {
  localStorage.removeItem(TOKEN_NAME);
  localStorage.removeItem(USER_ID_KEY);

  goToLogin();
};

export const addZero = (num: number): string => {
  return num < 10 ? `0${num}` : num.toString();
};

export const getTimeDifference = (
  endDate: string | Date,
  startDate = new Date(),
): string | null => {
  if (!endDate) return null;

  const normalizeEndDate = endDate instanceof Date ? endDate : new Date(endDate);
  const normalizeStartDate = startDate instanceof Date ? startDate : new Date(startDate);

  const difference = differenceInMilliseconds(
    normalizeEndDate,
    normalizeStartDate,
  );

  if (difference < 0) return null;

  const { hours = 0, minutes = 0, seconds = 0 } = intervalToDuration({
    start: normalizeStartDate,
    end: normalizeEndDate,
  });

  if (!hours && !minutes && !seconds) {
    return null;
  }

  return `${addZero(hours)}:${addZero(minutes)}:${addZero(seconds)}`;
};

export const capitalizeFirstLetter = (str: string): string => {
  return str.charAt(0).toUpperCase() + str.slice(1);
};

export const getUserIdFromLocalStorage = (): number | null => {
  const userId = localStorage.getItem(USER_ID_KEY);

  if (!userId) {
    return null;
  }

  return parseInt(userId, 10);
};

export const getUserTokenFromLocalStorage = (): string | null => {
  return localStorage.getItem(TOKEN_NAME);
};
