import { TeacherActionTypes } from 'types/teacherActionTypes';
import TeacherState from 'types/teacherType';
import {
  GET_TEACHER_SUCCESS,
  RESET_TEACHER,
} from './actionTypes';

const initialState: TeacherState = {
  info: null,
};

export default (
  state = initialState,
  action: TeacherActionTypes,
): TeacherState => {
  switch (action.type) {
    case GET_TEACHER_SUCCESS:
      return {
        ...state,
        info: action.payload,
      };
    case RESET_TEACHER:
      return initialState;
    default: return state;
  }
};
