import { AppStateType } from 'types';
import UserState from 'types/userType';
import { EULA_VERSION, EULA_TYPE } from 'constants/common';

export const userSelector = (state: AppStateType): UserState => state.user;

export const getEULAVersion = (state: AppStateType): string | undefined => {
  const { user: { info: userInfo } } = state;
  const eulaType = userInfo?.teacher_is_candidate ? EULA_TYPE.CANDIDATE : EULA_TYPE.TEACHER;

  return EULA_VERSION[eulaType];
};

export const getIsUserAcceptedPolicyAgreement = (state: AppStateType): boolean | undefined => {
  const { user: { info: userInfo } } = state;
  const eulaVersion = getEULAVersion(state);

  return !eulaVersion || userInfo?.eula_version === eulaVersion;
};
