// teacher action creators

import {
  TeacherType,
} from 'types/common';

import {
  GetTeacherRequestActionType,
  GetTeacherSuccessActionType,
  ResetTeacherActionType,
} from 'types/teacherActionTypes';

import {
  GET_TEACHER_SUCCESS,
  GET_TEACHER_REQUEST,
  RESET_TEACHER,
} from './actionTypes';

export const teacherRequest = (
  teacherId: number,
): GetTeacherRequestActionType => ({
  type: GET_TEACHER_REQUEST,
  payload: teacherId,
});

export const teacherSuccess = (
  teacherInfo: TeacherType,
): GetTeacherSuccessActionType => ({
  type: GET_TEACHER_SUCCESS,
  payload: teacherInfo,
});

export const resetTeacher = (): ResetTeacherActionType => ({
  type: RESET_TEACHER,
});
