import {
  call,
  takeEvery,
  put,
} from 'typed-redux-saga';

import {
  fetchLibrarySections,
  fetchLibraryLinks,
} from 'utils/request';

import {
  stopLoading,
  librarySectionsSuccess,
  libraryLinksSuccess,
} from './actions';

import {
  GET_LIBRARY_PENDING,
} from './actionTypes';

function* loadLibrarySections() {
  try {
    const { data } = yield* call(fetchLibrarySections);

    yield* put(librarySectionsSuccess(data._embedded.sections));
  } catch (e) {
    yield* put(stopLoading());
  }
}

function* LibrarySectionsSaga() {
  yield* takeEvery(GET_LIBRARY_PENDING, loadLibrarySections);
}

function* loadLibraryLinks() {
  try {
    const { data } = yield* call(fetchLibraryLinks);

    yield* put(libraryLinksSuccess(data._embedded.links));
  } catch (e) {
    yield* put(stopLoading());
  }
}

function* libraryLinksSaga() {
  yield* takeEvery(GET_LIBRARY_PENDING, loadLibraryLinks);
}

export default [
  LibrarySectionsSaga(),
  libraryLinksSaga(),
];
