import React, {
  lazy,
  FC,
} from 'react';
import { Switch, Redirect } from 'react-router-dom';
import EmptyLayout from 'layouts/EmptyLayout';
import { EnhancedRouteProps } from 'types/common';

import Route from './EnhancedRoute';
import paths from './paths';

const Dashboard = lazy(() => import('pages/Dashboard'));
const Schedule = lazy(() => import('pages/Schedule'));
const Library = lazy(() => import('pages/Library'));
const Program = lazy(() => import('pages/Program'));
const Account = lazy(() => import('pages/Account'));
const Portal = lazy(() => import('pages/Portal'));
const Signin = lazy(() => import('pages/Signin'));

const routes: EnhancedRouteProps[] = [
  {
    path: paths.dashboard,
    component: Dashboard,
    exact: true,
    protected: true,
  },
  {
    path: paths.schedule,
    component: Schedule,
    exact: true,
    protected: true,
  },
  {
    path: paths.library,
    component: Library,
    exact: true,
    protected: true,
  },
  {
    path: paths.program,
    component: Program,
    exact: true,
    protected: true,
  },
  {
    path: paths.account,
    component: Account,
    exact: true,
    protected: true,
  },
  {
    path: paths.portal,
    component: Portal,
    exact: true,
    protected: true,
  },
  {
    path: paths.signin,
    component: Signin,
    exact: true,
    layout: EmptyLayout,
  },
];

const Routes: FC = () => (
  <Switch>
    {routes.map((route, index) => (
      <Route
        key={index} // eslint-disable-line react/no-array-index-key
        {...route}
      />
    ))}
    <Redirect from="*" to={paths.dashboard} />
  </Switch>
);

export default Routes;
