import {
  stopLoading,
  levelSuccess,
  lessonPending,
  levelPending,
  unitPending,
  unitSuccess,
  lessonSuccess,
} from 'store/program/actions';

import {
  call,
  all,
  takeEvery,
  put,
} from 'typed-redux-saga';

import {
  fetchCurriculumLevels,
  fetchCurriculumUnits,
  fetchCurriculumLessons,
} from 'utils/request';

import {
  GET_PROGRAM_PENDING,
  GET_LEVEL_PENDING,
  GET_UNIT_PENDING,
  GET_LESSON_PENDING,
} from './actionTypes';

function* loadProgramData() {
  try {
    yield* all([
      put(lessonPending()),
      put(levelPending()),
      put(unitPending()),
    ]);
  } catch (e) {
    yield* put(stopLoading());
  }
}

function* programSaga() {
  yield* takeEvery(GET_PROGRAM_PENDING, loadProgramData);
}

function* loadLevels() {
  try {
    const response = yield* call(fetchCurriculumLevels);
    yield* put(levelSuccess(response.data._embedded.levels));
  } catch (e) {
    yield* put(stopLoading());
  }
}

function* levelSaga() {
  yield* takeEvery(GET_LEVEL_PENDING, loadLevels);
}

function* loadUnits() {
  try {
    const response = yield* call(fetchCurriculumUnits);
    yield* put(unitSuccess(response.data._embedded.units));
  } catch (e) {
    yield* put(stopLoading());
  }
}

function* unitSaga() {
  yield* takeEvery(GET_UNIT_PENDING, loadUnits);
}

function* loadLessons() {
  try {
    const response = yield* call(fetchCurriculumLessons);
    yield* put(lessonSuccess(response.data._embedded.lessons));
  } catch (e) {
    yield* put(stopLoading());
  }
}

function* lessonSaga() {
  yield* takeEvery(GET_LESSON_PENDING, loadLessons);
}

export default [
  programSaga(),
  levelSaga(),
  unitSaga(),
  lessonSaga(),
];
