import React, {
  FC,
  useState,
  useEffect,
  useCallback,
  memo,
  ChangeEvent,
} from 'react';
import { useSelector, useDispatch } from 'react-redux';
import classnames from 'classnames/bind';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';

import Modal from 'components/Modal';
import Button from 'components/Button';
import { getEULAVersion, getIsUserAcceptedPolicyAgreement } from 'store/user/selectors';
import { updateUserRequest } from 'store/user/actions';
import { POLICIES_TYPES } from 'constants/policies';
import { logout } from 'utils/helpers';
import { UserType, RegionType } from 'types/common';

import { POLICIES_CONFIG } from './config';
import DeclineAgreementModal from './DeclineAgreementModal';
import styles from './styles.module.scss';

const cx = classnames.bind(styles);

interface IPoliciesStatus {
  [key: string]: boolean;
}

interface PropsType {
  userInfo: UserType | null,
  regionInfo: RegionType | null,
}

const PolicyAgreementModal: FC<PropsType> = (props) => {
  const { userInfo, regionInfo } = props;

  const [isAgreementModalOpen, setIsAgreementModalOpen] = useState(false);
  const [isDeclineAgreementModalOpen, setIsDeclineAgreementModalOpen] = useState(false);
  const [policiesAcceptededStatus, setPoliciesAcceptededStatus] = useState<IPoliciesStatus>(
    Object.values(POLICIES_TYPES).reduce((acc, policyType) => ({
      ...acc,
      [policyType]: false,
    }), {}),
  );

  const dispatch = useDispatch();
  const isUserAcceptedPolicyAgreement = useSelector(getIsUserAcceptedPolicyAgreement);
  const eulaVersion = useSelector(getEULAVersion);

  const openAgreementModal = useCallback(() => {
    setIsAgreementModalOpen(true);
  }, []);

  const closeAgreementModal = useCallback(() => {
    setIsAgreementModalOpen(false);
  }, []);

  const openDeclineAgreementModal = useCallback(() => {
    setIsDeclineAgreementModalOpen(true);
  }, []);

  const closeDeclineAgreementModal = useCallback(() => {
    setIsDeclineAgreementModalOpen(false);
  }, []);

  useEffect(() => {
    if (!userInfo || isUserAcceptedPolicyAgreement) {
      closeAgreementModal();
      return;
    }

    openAgreementModal();
  }, [userInfo, isUserAcceptedPolicyAgreement]);

  const handleAgreementModalClose = useCallback(() => {
    closeAgreementModal();
  }, []);

  const handleCheckboxToggle = useCallback((
    event: ChangeEvent<HTMLInputElement>,
    isChecked: boolean,
  ) => {
    const { name } = event.target;

    setPoliciesAcceptededStatus((prevPoliciesAcceptededStatus) => ({
      ...prevPoliciesAcceptededStatus,
      [name]: isChecked,
    }));
  }, []);

  const handlePolicyAgreementDecline = useCallback(() => {
    openDeclineAgreementModal();
  }, []);

  const handlePolicyAgreementConfirm = useCallback(() => {
    dispatch(updateUserRequest({
      eula_version: eulaVersion,
    }));
  }, [dispatch, eulaVersion]);

  const handleDeclineAgreementModalClose = useCallback(() => {
    closeDeclineAgreementModal();
  }, []);

  const handleDecline = useCallback(() => {
    closeDeclineAgreementModal();
    closeAgreementModal();
    logout();
  }, []);

  const areAllPoliciesAccepted = Object
    .values(policiesAcceptededStatus)
    .every((isPolicyAccepted) => isPolicyAccepted);

  return (
    <>
      <Modal
        title="The Agreement for the processing of personal data"
        onClose={handleAgreementModalClose}
        open={isAgreementModalOpen}
        disableEscapeKeyDown
        disableBackdropClick
        shouldShowCloseButton={false}
        className={cx('modal')}
      >
        <p>
          <strong>
            To proceed, please read and accept terms and appendixes by
            selecting all checkboxes below.
          </strong>
        </p>
        <FormGroup>
          {POLICIES_CONFIG.map((policy) => {
            const {
              key,
              labelPrefix,
              getLinkText,
              getUrl,
            } = policy;

            return (
              <FormControlLabel
                key={key}
                control={(
                  <Checkbox
                    checked={policiesAcceptededStatus[key]}
                    onChange={handleCheckboxToggle}
                    name={key}
                  />
                )}
                label={(
                  <span>
                    {labelPrefix}
                    <a
                      href={getUrl(userInfo, regionInfo)}
                      target="_blank"
                      rel="noreferrer"
                    >
                      {getLinkText(userInfo)}
                    </a>
                  </span>
                )}
              />
            );
          })}
        </FormGroup>

        <div className={cx('footer')}>
          <Button
            text="Decline"
            className={cx('button')}
            onClick={handlePolicyAgreementDecline}
            type="button"
          />

          <Button
            theme="primary"
            text="Accept"
            disabled={!areAllPoliciesAccepted}
            className={cx('button')}
            onClick={handlePolicyAgreementConfirm}
            type="button"
          />
        </div>
      </Modal>

      <DeclineAgreementModal
        isOpen={isDeclineAgreementModalOpen}
        onClose={handleDeclineAgreementModalClose}
        onConfirm={handleDecline}
      />
    </>
  );
};

export default memo(PolicyAgreementModal);
