import {
  call,
  takeEvery,
  put,
} from 'typed-redux-saga';

import { fetchRegion } from 'utils/request';
import { RegionPendingAction } from 'types/regionActionTypes';
import { GET_REGION_PENDING } from './actionTypes';
import { regionSuccess } from './actions';

function* loadRegionInfo(
  { payload: { regionCode } }: RegionPendingAction,
) {
  try {
    const response = yield* call(fetchRegion, regionCode);
    const { data: region } = response;

    yield* put(regionSuccess(region));
  } catch (e) {
    // eslint-disable-next-line
    console.error(e);
  }
}

function* regionSaga() {
  yield* takeEvery(GET_REGION_PENDING, loadRegionInfo);
}

export default [
  regionSaga(),
];
