import { NormalizedCompletedClassType, ProfileInformationType } from 'types/common';
import {
  DashboardStopLoadingAction,
  DashboardPendingAction,
  CompletedClassesSuccessAction,
  EstimationTagsSuccessAction,
  MoreCompletedClassesPendingAction,
  MoreCompletedClassesSuccessAction,
  NextClassCurriculumPendingAction,
  NextClassCurriculumSuccessAction,
  NextClassFeedbackPendingAction,
  NextClassFeedbackSuccessAction,
  RegionPendingAction,
  RegionSuccessAction,
  StatisticsSuccessAction,
  TeacherSuccessAction,
  UpcomingClassCurriculumPendingAction,
  UpcomingClassCurriculumSuccessAction,
  UpcomingClassesSuccessAction,
  UpcomingClassFeedbackPendingAction,
  UpcomingClassFeedbackSuccessAction,
  UserSuccessAction,
  ResetDashboardActionType,
} from 'types/dashboardActionTypes';

import {
  EstimationTags,
  FeedbackType,
  NextClassType,
  StatisticsType,
  UpcomingClassesType,
} from 'types/dashboardType';

import {
  GET_UPCOMING_CLASS_CURRICULUM_PENDING,
  GET_UPCOMING_CLASS_FEEDBACK_PENDING,
  GET_MORE_COMPLETED_CLASSES_PENDING,
  GET_NEXT_CLASS_CURRICULUM_PENDING,
  GET_NEXT_CLASS_FEEDBACK_PENDING,
  GET_DASHBOARD_PENDING,
  GET_REGION_PENDING,

  GET_UPCOMING_CLASS_CURRICULUM_SUCCESS,
  GET_UPCOMING_CLASS_FEEDBACK_SUCCESS,
  GET_MORE_COMPLETED_CLASSES_SUCCESS,
  GET_NEXT_CLASS_CURRICULUM_SUCCESS,
  GET_NEXT_CLASS_FEEDBACK_SUCCESS,
  GET_COMPLETED_CLASSES_SUCCESS,
  GET_UPCOMING_CLASSES_SUCCESS,
  GET_ESTIMATION_TAGS_SUCCESS,
  GET_STATISTICS_SUCCESS,
  GET_TEACHER_SUCCESS,
  GET_REGION_SUCCESS,
  GET_USER_SUCCESS,

  DASHBOARD_STOP_LOADING,
  RESET_DASHBOARD,
} from './actionTypes';

export const dashboardPending = (): DashboardPendingAction => ({
  type: GET_DASHBOARD_PENDING,
});

export const regionPending = (regionCode: string): RegionPendingAction => ({
  type: GET_REGION_PENDING,
  payload: { regionCode },
});

export const nextClassFeedbackPending = (classId: number): NextClassFeedbackPendingAction => ({
  type: GET_NEXT_CLASS_FEEDBACK_PENDING,
  payload: { classId },
});

export const nextClassCurriculumPending = (lessonId: number): NextClassCurriculumPendingAction => ({
  type: GET_NEXT_CLASS_CURRICULUM_PENDING,
  payload: { lessonId },
});

export const moreCompletedClassesPending = (
  nextLink: null | string,
): MoreCompletedClassesPendingAction => ({
  type: GET_MORE_COMPLETED_CLASSES_PENDING,
  payload: { nextLink },
});

export const upcomingClassCurriculumPending = (
  classId: number, lessonId: number,
): UpcomingClassCurriculumPendingAction => ({
  type: GET_UPCOMING_CLASS_CURRICULUM_PENDING,
  payload: { classId, lessonId },
});

export const upcomingClassFeedbackPending = (
  classId: number,
): UpcomingClassFeedbackPendingAction => ({
  type: GET_UPCOMING_CLASS_FEEDBACK_PENDING,
  payload: { classId },
});

export const userSuccess = (
  profileInformation: ProfileInformationType,
): UserSuccessAction => ({
  type: GET_USER_SUCCESS,
  payload: { profileInformation },
});

export const teacherSuccess = (
  profileInformation: ProfileInformationType,
): TeacherSuccessAction => ({
  type: GET_TEACHER_SUCCESS,
  payload: { profileInformation },
});

export const regionSuccess = (
  profileInformation: ProfileInformationType,
): RegionSuccessAction => ({
  type: GET_REGION_SUCCESS,
  payload: { profileInformation },
});

export const statisticsSuccess = (
  statistics: StatisticsType,
  feedback: FeedbackType,
): StatisticsSuccessAction => ({
  type: GET_STATISTICS_SUCCESS,
  payload: { statistics, feedback },
});

export const estimationTagsSuccess = (tags: EstimationTags): EstimationTagsSuccessAction => ({
  type: GET_ESTIMATION_TAGS_SUCCESS,
  payload: { tags },
});

export const upcomingClassesSuccess = (
  upcomingClasses: UpcomingClassesType,
  nextClass: NextClassType | null,
): UpcomingClassesSuccessAction => ({
  type: GET_UPCOMING_CLASSES_SUCCESS,
  payload: { upcomingClasses, nextClass },
});

export const completedClassesSuccess = (
  completedClasses: NormalizedCompletedClassType[],
  nextCompletedClassesLink: string | null,
): CompletedClassesSuccessAction => ({
  type: GET_COMPLETED_CLASSES_SUCCESS,
  payload: { completedClasses, nextCompletedClassesLink },
});

export const moreCompletedClassesSuccess = (
  completedClasses: NormalizedCompletedClassType[],
  nextCompletedClassesLink: string | null,
): MoreCompletedClassesSuccessAction => ({
  type: GET_MORE_COMPLETED_CLASSES_SUCCESS,
  payload: { completedClasses, nextCompletedClassesLink },
});

export const nextClassCurriculumSuccess = (
  lessonTitle: string,
): NextClassCurriculumSuccessAction => ({
  type: GET_NEXT_CLASS_CURRICULUM_SUCCESS,
  payload: { lessonTitle },
});

export const upcomingClassCurriculumSuccess = (
  lessonTitle: string,
  classId: number,
): UpcomingClassCurriculumSuccessAction => ({
  type: GET_UPCOMING_CLASS_CURRICULUM_SUCCESS,
  payload: { lessonTitle, classId },
});

export const upcomingClassFeedbackSuccess = (
  feedback: string,
  classId: number,
): UpcomingClassFeedbackSuccessAction => ({
  type: GET_UPCOMING_CLASS_FEEDBACK_SUCCESS,
  payload: { feedback, classId },
});

export const nextClassFeedbackSuccess = (
  nextClassFeedback: string,
): NextClassFeedbackSuccessAction => ({
  type: GET_NEXT_CLASS_FEEDBACK_SUCCESS,
  payload: { nextClassFeedback },
});

export const stopLoading = (): DashboardStopLoadingAction => ({
  type: DASHBOARD_STOP_LOADING,
});

export const resetDashboard = (): ResetDashboardActionType => ({
  type: RESET_DASHBOARD,
});
