import { UserActionTypes } from 'types/userActionTypes';
import UserState from 'types/userType';
import {
  GET_USER_SUCCESS,
  RESET_USER,
} from './actionTypes';

const initialState: UserState = {
  info: null,
};

export default (
  state = initialState,
  action: UserActionTypes,
): UserState => {
  switch (action.type) {
    case GET_USER_SUCCESS:
      return {
        ...state,
        info: action.payload,
      };
    case RESET_USER:
      return initialState;
    default: return state;
  }
};
