// user action creators

import {
  UserType,
  ChangeUserInfoRequestParams,
} from 'types/common';

import {
  GetUserRequestActionType,
  GetUserSuccessActionType,
  UpdateUserInfoActionType,
  ResetUserActionType,
} from 'types/userActionTypes';

import {
  GET_USER_SUCCESS,
  GET_USER_REQUEST,
  UPDATE_USER_REQUEST,
  RESET_USER,
} from './actionTypes';

export const userRequest = (
  userId: number,
): GetUserRequestActionType => ({
  type: GET_USER_REQUEST,
  payload: userId,
});

export const updateUserRequest = (
  params: ChangeUserInfoRequestParams,
): UpdateUserInfoActionType => ({
  type: UPDATE_USER_REQUEST,
  payload: params,
});

export const userSuccess = (
  userInfo: UserType,
): GetUserSuccessActionType => ({
  type: GET_USER_SUCCESS,
  payload: userInfo,
});

export const resetUser = (): ResetUserActionType => ({
  type: RESET_USER,
});
