// auth action creators

import {
  AuthTokenRequestType,
  AuthTokenResponseType,
  BackendResponseErrorType,
} from 'types/common';

import {
  LoginRequestActionType,
  LoginSuccessActionType,
  LoginFailActionType,
  LogoutSuccessActionType,
  ResetAuthActionType,
} from 'types/authActionTypes';

import {
  LOGIN_REQUEST,
  LOGIN_SUCCESS,
  LOGIN_FAIL,
  LOGOUT_SUCCESS,
  RESET_AUTH,
} from './actionTypes';

export const loginRequest = (
  params: AuthTokenRequestType,
): LoginRequestActionType => ({
  type: LOGIN_REQUEST,
  payload: params,
});

export const loginSuccess = (
  userInfo: AuthTokenResponseType,
): LoginSuccessActionType => ({
  type: LOGIN_SUCCESS,
  payload: userInfo,
});

export const loginFail = (
  error: BackendResponseErrorType,
): LoginFailActionType => ({
  type: LOGIN_FAIL,
  payload: error,
});

export const logoutSuccess = (): LogoutSuccessActionType => ({
  type: LOGOUT_SUCCESS,
});

export const resetAuth = (): ResetAuthActionType => ({
  type: RESET_AUTH,
});
