import { ScheduleActionTypes } from 'types/scheduleActionTypes';
import { ScheduleState } from 'types/scheduleType';
import {
  GET_SCHEDULE_PENDING,
  SCHEDULE_STOP_LOADING,
  GET_CLASSES_SUCCESS,
  GET_CLASSES_HISTORY_SUCCESS,
  GET_WEEKLY_SLOTS_SUCCESS,
  OPERATE_SLOTS_PENDING,
  OPERATE_SLOTS_SUCCESS,
  RESET_SCHEDULE,
} from './actionTypes';

const initialState: ScheduleState = {
  weeklySlots: [],
  weeklySlotsLoading: false,
  classes: [],
  classesLoading: false,
  classesHistory: [],
  classesHistoryLoading: false,
  operationWithSlotsIsSuccess: false,
};

export default (
  state = initialState,
  action: ScheduleActionTypes,
): ScheduleState => {
  switch (action.type) {
    case GET_SCHEDULE_PENDING:
      return {
        ...state,
        weeklySlotsLoading: true,
        classesLoading: true,
        classesHistoryLoading: true,
      };
    case SCHEDULE_STOP_LOADING:
      return {
        ...state,
        weeklySlotsLoading: false,
        classesLoading: false,
      };
    case GET_WEEKLY_SLOTS_SUCCESS:
      return {
        ...state,
        weeklySlotsLoading: false,
        weeklySlots: action.payload.slots,
      };
    case GET_CLASSES_SUCCESS:
      return {
        ...state,
        classesLoading: false,
        classes: action.payload.classes,
      };
    case GET_CLASSES_HISTORY_SUCCESS:
      return {
        ...state,
        classesHistoryLoading: false,
        classesHistory: action.payload.classesHistory,
      };
    case OPERATE_SLOTS_PENDING:
      return {
        ...state,
        operationWithSlotsIsSuccess: false,
      };
    case OPERATE_SLOTS_SUCCESS:
      return {
        ...state,
        operationWithSlotsIsSuccess: true,
      };
    case RESET_SCHEDULE:
      return initialState;
    default: return state;
  }
};
