import {
  ClassHistoryType,
  ClassRequestParams,
  ClassType,
  OperationRequstParams,
  WeeklySlotType,
} from 'types/common';
import {
  ClassesSuccessActionType,
  HistoryClassesSuccessActionType,
  SchedulePendingActionType,
  ScheduleStopLoadingActionType,
  WeeklySlotsSuccessActionType,
  OperateSlotsActionType,
  OperateSlotsSuccessActionType,
  ResetScheduleActionType,
} from 'types/scheduleActionTypes';

import {
  GET_SCHEDULE_PENDING,
  SCHEDULE_STOP_LOADING,
  GET_WEEKLY_SLOTS_SUCCESS,
  GET_CLASSES_SUCCESS,
  GET_CLASSES_HISTORY_SUCCESS,
  OPERATE_SLOTS_SUCCESS,
  OPERATE_SLOTS_PENDING,
  RESET_SCHEDULE,
} from './actionTypes';

type SheduleParams = {
  teacherClassesParams: ClassRequestParams,
  historyClassesParams: ClassRequestParams,
};

export const schedulePending = (params: SheduleParams): SchedulePendingActionType => ({
  type: GET_SCHEDULE_PENDING,
  payload: params,
});

export const stopLoading = (): ScheduleStopLoadingActionType => ({
  type: SCHEDULE_STOP_LOADING,
});

export const weeklySlotsSuccess = (
  slots: WeeklySlotType[],
): WeeklySlotsSuccessActionType => ({
  type: GET_WEEKLY_SLOTS_SUCCESS,
  payload: { slots },
});

export const classesSuccess = (
  classes: ClassType[],
): ClassesSuccessActionType => ({
  type: GET_CLASSES_SUCCESS,
  payload: { classes },
});

export const historyClassesSuccess = (
  classesHistory: ClassHistoryType[],
): HistoryClassesSuccessActionType => ({
  type: GET_CLASSES_HISTORY_SUCCESS,
  payload: { classesHistory },
});

export const operateSlots = (params: OperationRequstParams): OperateSlotsActionType => ({
  type: OPERATE_SLOTS_PENDING,
  payload: params,
});

export const operateSlotsSuccess = (): OperateSlotsSuccessActionType => ({
  type: OPERATE_SLOTS_SUCCESS,
  payload: true,
});

export const resetSchedule = (): ResetScheduleActionType => ({
  type: RESET_SCHEDULE,
});
