import { LibraryLinkType, LibrarySectionType } from 'types/common';
import {
  LibrarySectionsSuccessAction,
  LibraryLinksSuccessAction,
  LibraryPendingAction,
  LibraryStopLoadingAction,
  ResetLibraryActionType,
} from 'types/libraryActionTypes';

import {
  GET_LIBRARY_PENDING,
  LIBRARY_STOP_LOADING,
  GET_LIBRARY_SECTIONS_SUCCESS,
  GET_LIBRARY_LINKS_SUCCESS,
  RESET_LIBRARY,
} from './actionTypes';

export const libraryPending = (): LibraryPendingAction => ({
  type: GET_LIBRARY_PENDING,
});

export const stopLoading = (): LibraryStopLoadingAction => ({
  type: LIBRARY_STOP_LOADING,
});

export const librarySectionsSuccess = (
  sections: LibrarySectionType[],
): LibrarySectionsSuccessAction => ({
  type: GET_LIBRARY_SECTIONS_SUCCESS,
  payload: { sections },
});

export const libraryLinksSuccess = (
  links: LibraryLinkType[],
): LibraryLinksSuccessAction => ({
  type: GET_LIBRARY_LINKS_SUCCESS,
  payload: { links },
});

export const resetLibrary = (): ResetLibraryActionType => ({
  type: RESET_LIBRARY,
});
