import {
  ChangeUserInfoRequestParams,
  HealthType,
  RegionType,
  TeacherType,
  TimezoneType,
  TransactionType,
  UserType,
} from 'types/common';

import {
  AccountPendingAction,
  RegionSuccessAction,
  AccountStopLoadingAction,
  UserSuccessAction,
  TeacherSuccessAction,
  RegionPendingAction,
  TransactionsSuccessAction,
  HealthHistorySuccessAction,
  TimezoneSuccessAction,
  ChangeUserInfoSuccessActionType,
  ChangeUserInfoActionType,
  ResetAccountActionType,
} from 'types/accountActionTypes';

import {
  GET_ACCOUNT_PENDING,
  ACCOUNT_STOP_LOADING,
  GET_USER_SUCCESS,
  GET_TEACHER_SUCCESS,
  GET_REGION_SUCCESS,
  GET_REGION_PENDING,
  GET_TRANSACTIONS_SUCCESS,
  GET_HEALTH_HISTORY_SUCCESS,
  GET_TIMEZONES_SUCCESS,
  CHANGE_USER_INFO_SUCCESS,
  CHANGE_USER_INFO_PENDING,
  RESET_ACCOUNT,
} from './actionTypes';

export const accountPending = (): AccountPendingAction => ({
  type: GET_ACCOUNT_PENDING,
});

export const stopLoading = (): AccountStopLoadingAction => ({
  type: ACCOUNT_STOP_LOADING,
});

export const userSuccess = (
  userInfo: UserType,
): UserSuccessAction => ({
  type: GET_USER_SUCCESS,
  payload: { userInfo },
});

export const changeUserInfoAction = (
  params: ChangeUserInfoRequestParams,
): ChangeUserInfoActionType => ({
  type: CHANGE_USER_INFO_PENDING,
  payload: params,
});

export const changeUserInfoSuccess = (
  userInfo: UserType,
): ChangeUserInfoSuccessActionType => ({
  type: CHANGE_USER_INFO_SUCCESS,
  payload: userInfo,
});

export const teacherSuccess = (
  teacherInfo: TeacherType,
): TeacherSuccessAction => ({
  type: GET_TEACHER_SUCCESS,
  payload: { teacherInfo },
});

export const regionPending = (regionCode: string): RegionPendingAction => ({
  type: GET_REGION_PENDING,
  payload: { regionCode },
});

export const regionSuccess = (
  regionInfo: RegionType,
): RegionSuccessAction => ({
  type: GET_REGION_SUCCESS,
  payload: { regionInfo },
});

export const transactionsSuccess = (
  transactions: TransactionType[],
): TransactionsSuccessAction => ({
  type: GET_TRANSACTIONS_SUCCESS,
  payload: { transactions },
});

export const healthHistorySuccess = (
  healthHistory: HealthType[],
): HealthHistorySuccessAction => ({
  type: GET_HEALTH_HISTORY_SUCCESS,
  payload: { healthHistory },
});

export const timezonesSuccess = (
  timezones: TimezoneType[],
): TimezoneSuccessAction => ({
  type: GET_TIMEZONES_SUCCESS,
  payload: { timezones },
});

export const resetAccount = (): ResetAccountActionType => ({
  type: RESET_ACCOUNT,
});
