import {
  call,
  takeEvery,
  put,
} from 'typed-redux-saga';

import {
  fetchUserInfo,
  changeUserInfo,
} from 'utils/request';

import {
  GetUserRequestActionType,
  UpdateUserInfoActionType,
} from 'types/userActionTypes';
import {
  GET_USER_REQUEST,
  UPDATE_USER_REQUEST,
} from './actionTypes';
import {
  userSuccess,
} from './actions';

function* loadUserInfo({ payload: userId }: GetUserRequestActionType) {
  try {
    const { data: user } = yield* call(fetchUserInfo, userId);

    yield* put(userSuccess(user));
  } catch (e) {
    // console.log(e);
  }
}

function* updateUserInfo({ payload }: UpdateUserInfoActionType) {
  try {
    const { data: user } = yield* call(changeUserInfo, payload);

    yield* put(userSuccess(user));
  } catch (e) {
    console.error(e);
  }
}

function* userSaga() {
  yield* takeEvery(GET_USER_REQUEST, loadUserInfo);
}

function* updateUserSaga() {
  yield* takeEvery(UPDATE_USER_REQUEST, updateUserInfo);
}

export default [
  userSaga(),
  updateUserSaga(),
];
