import React, { FC, memo } from 'react';
import classnames from 'classnames/bind';

import Modal from 'components/Modal';
import Button from 'components/Button';
import styles from './styles.module.scss';

const cx = classnames.bind(styles);

interface PropsType {
  isOpen: boolean;
  onClose: () => void;
  onConfirm: () => void;
}

const DeclineAgreementModal: FC<PropsType> = (props) => {
  const { isOpen, onClose, onConfirm } = props;

  return (
    <Modal
      title="The Agreement for the processing of personal data"
      onClose={onClose}
      open={isOpen}
      className={cx('modal')}
    >
      <p>
        To continue cooperation with Novakid you need to choose the &quot;Accept&quot; option.
        In case you do not agree with any of the provisions, please email your concerns
        at&nbsp;
        <a href="mailto:teacherops@novakidschool.com">
          teacherops@novakidschool.com
        </a>
      </p>

      <div className={cx('footer')}>
        <Button
          theme="primary"
          text="Yes"
          className={cx('button')}
          onClick={onConfirm}
          type="button"
        />
        <Button
          text="No"
          className={cx('button')}
          onClick={onClose}
          type="button"
        />
      </div>
    </Modal>
  );
};

export default memo(DeclineAgreementModal);
