import { RegionActionTypes } from 'types/regionActionTypes';
import RegionState from 'types/regionType';
import {
  GET_REGION_SUCCESS,
  RESET_REGION,
} from './actionTypes';

const initialState: RegionState = {
  info: null,
};

export default (
  state = initialState,
  action: RegionActionTypes,
): RegionState => {
  switch (action.type) {
    case GET_REGION_SUCCESS:
      return {
        ...state,
        info: action.payload.regionInfo,
      };
    case RESET_REGION:
      return initialState;
    default: return state;
  }
};
