import {
  stopLoading,
  userSuccess,
  regionPending,
  regionSuccess,
  teacherSuccess,
  transactionsSuccess,
  healthHistorySuccess,
  timezonesSuccess,
  changeUserInfoSuccess,
} from 'store/account/actions';
import {
  call,
  takeEvery,
  put,
} from 'typed-redux-saga';
import { ChangeUserInfoActionType } from 'types/accountActionTypes';

import {
  fetchRegion,
  fetchTeacherInfo,
  fetchTransactions,
  fetchUserInfo,
  fetchHealthHistory,
  fetchTimezones,
  changeUserInfo,
} from 'utils/request';
import {
  GET_ACCOUNT_PENDING,
  GET_REGION_PENDING,
  CHANGE_USER_INFO_PENDING,
} from './actionTypes';

function* loadUserInfo() {
  try {
    const { data: user } = yield* call(fetchUserInfo);
    yield* put(userSuccess(user));

    yield* put(regionPending(user.region_code));
  } catch (e) {
    yield* put(stopLoading());
  }
}

function* userSaga() {
  yield* takeEvery(GET_ACCOUNT_PENDING, loadUserInfo);
}

function* loadTeacherInfo() {
  try {
    const { data: teacher } = yield* call(fetchTeacherInfo);

    yield* put(teacherSuccess(teacher));
  } catch (e) {
    yield* put(stopLoading());
  }
}

function* teacherSaga() {
  yield* takeEvery(GET_ACCOUNT_PENDING, loadTeacherInfo);
}

function* loadRegionInfo(
  { payload: { regionCode } }: { type: string, payload: { regionCode: string } },
) {
  try {
    const response = yield* call(fetchRegion, regionCode);
    const { data: region } = response;

    yield* put(regionSuccess(region));
  } catch (e) {
    yield* put(stopLoading());
  }
}

function* regionPendingSaga() {
  yield* takeEvery(GET_REGION_PENDING, loadRegionInfo);
}

function* loadTransactions() {
  try {
    const { data } = yield* call(fetchTransactions);

    yield* put(transactionsSuccess(data._embedded.transactions));
  } catch (e) {
    yield* put(stopLoading());
  }
}

function* transactionsSaga() {
  yield* takeEvery(GET_ACCOUNT_PENDING, loadTransactions);
}

function* loadHealthHistory() {
  try {
    const { data } = yield* call(fetchHealthHistory);

    yield* put(healthHistorySuccess(data._embedded.teacher_health));
  } catch (e) {
    yield* put(stopLoading());
  }
}

function* healthHistorySaga() {
  yield* takeEvery(GET_ACCOUNT_PENDING, loadHealthHistory);
}

function* loadTimezones() {
  try {
    const { data } = yield* call(fetchTimezones);

    yield* put(timezonesSuccess(data._embedded.timezones));
  } catch (e) {
    yield* put(stopLoading());
  }
}

function* timezonesSaga() {
  yield* takeEvery(GET_ACCOUNT_PENDING, loadTimezones);
}

function* changeUserData({ payload }: ChangeUserInfoActionType) {
  try {
    const { data } = yield* call(changeUserInfo, payload);

    yield* put(changeUserInfoSuccess(data));
  } catch (e) {
    // console.log(e);
  }
}

function* changeUserInfoSaga() {
  yield* takeEvery(CHANGE_USER_INFO_PENDING, changeUserData);
}

export default [
  teacherSaga(),
  userSaga(),
  regionPendingSaga(),
  transactionsSaga(),
  healthHistorySaga(),
  timezonesSaga(),
  changeUserInfoSaga(),
];
