import { AuthActionTypes } from 'types/authActionTypes';
import AuthState from 'types/authType';
import {
  LOGIN_REQUEST,
  LOGIN_SUCCESS,
  LOGIN_FAIL,
  RESET_AUTH,
} from './actionTypes';

const initialState: AuthState = {
  isPending: false,
  error: null,
};

export default (
  state = initialState,
  action: AuthActionTypes,
): AuthState => {
  switch (action.type) {
    case LOGIN_REQUEST:
      return {
        ...state,
        isPending: true,
      };
    case LOGIN_SUCCESS:
      return {
        ...state,
        isPending: false,
      };
    case LOGIN_FAIL:
      return {
        ...state,
        error: action.payload,
        isPending: false,
      };
    case RESET_AUTH:
      return initialState;
    default: return state;
  }
};
