import {
  call,
  takeEvery,
  put,
  all,
} from 'typed-redux-saga';

import {
  fetchMoreCompletedClasses,
  fetchCompletedClasses,
  fetchCurriculumLesson,
  fetchUpcomingClasses,
  fetchEstimationTags,
  fetchClassFeedback,
  fetchTeacherInfo,
  fetchStatistics,
  fetchUserInfo,
  fetchRegion,
} from 'utils/request';
import {
  normalizeCompletedClass,
  normalizeUpcomingClass,
  normalizeNextClass,
  getNextClass,
} from 'utils/helpers';
import { NextClassType } from 'types/dashboardType';
import {
  GET_UPCOMING_CLASS_CURRICULUM_PENDING,
  GET_UPCOMING_CLASS_FEEDBACK_PENDING,
  GET_MORE_COMPLETED_CLASSES_PENDING,
  GET_NEXT_CLASS_CURRICULUM_PENDING,
  GET_NEXT_CLASS_FEEDBACK_PENDING,
  GET_DASHBOARD_PENDING,
  GET_REGION_PENDING,
} from './actionTypes';
import {
  upcomingClassCurriculumSuccess,
  upcomingClassFeedbackSuccess,
  moreCompletedClassesSuccess,
  nextClassCurriculumSuccess,
  nextClassFeedbackSuccess,
  completedClassesSuccess,
  upcomingClassesSuccess,
  estimationTagsSuccess,
  statisticsSuccess,
  teacherSuccess,
  regionSuccess,
  userSuccess,

  nextClassCurriculumPending,
  nextClassFeedbackPending,
  regionPending,
  stopLoading,
} from './actions';

function* loadUserInfo() {
  try {
    const { data: user } = yield* call(fetchUserInfo);
    const {
      teacher_account_health,
      class_cost,
      balance,
      region_code: regionCode,
    } = user;

    yield* put(userSuccess({
      accountHealth: parseInt(teacher_account_health, 10),
      classCost: `$${class_cost}`,
      balance: `${balance} classes ($${+balance * +class_cost})`,
    }));

    yield* put(regionPending(regionCode));
  } catch (e) {
    yield* put(stopLoading());
  }
}

function* userSaga() {
  yield* takeEvery(GET_DASHBOARD_PENDING, loadUserInfo);
}

function* loadTeacherInfo() {
  try {
    const response = yield* call(fetchTeacherInfo);
    const teacher = response.data;
    const { name } = teacher;
    const { biography, education } = teacher?._embedded?.localized_info[0] || {};
    const { href: imgSrc } = teacher?._links?.photo_content || {};

    yield* put(teacherSuccess({
      name,
      education: education || 'N/A',
      experience: biography || 'N/A',
      imgSrc,
    }));
  } catch (e) {
    yield* put(stopLoading());
  }
}

function* teacherSaga() {
  yield* takeEvery(GET_DASHBOARD_PENDING, loadTeacherInfo);
}

function* loadStatistics() {
  try {
    const response = yield* call(fetchStatistics);
    const classesStats = response.data?.classes_stat_data || {};
    const {
      completed_all_non_trial_count = 0,
      completed_trial_count = 0,
      rate_trial_students_count = 0,
      conversion_in_percent,
      prolonged_students_count = 0,
    } = classesStats;
    const conversion = conversion_in_percent ? parseInt(conversion_in_percent, 10) : 0;
    let level: string;

    if (conversion < 35) {
      level = 'Danger';
    } else if (conversion >= 35 && conversion <= 50) {
      level = 'Waring';
    } else {
      level = 'Low';
    }

    const feedbackStats = response?.data?.teacher_feedback_stat_data || {};

    const {
      feedback_thumb_downs,
      feedback_thumb_ups_freq = 0,
      tags_most_popular_thumb_ups = [],
      tags_most_popular_thumb_downs = [],
    } = feedbackStats;

    const valueUp = feedback_thumb_downs !== 0
      ? feedback_thumb_ups_freq
      : 100;

    const thumbUpTags = tags_most_popular_thumb_ups.map(({ id, freq }) => ({
      id,
      value: parseInt(freq, 10),
    }));

    const thumbDownTags = tags_most_popular_thumb_downs.map(({ id, freq }) => ({
      id,
      value: parseInt(freq, 10),
    }));

    const statistics = {
      students: prolonged_students_count,
      trial: completed_trial_count,
      regular: completed_all_non_trial_count,
      rateStudents: rate_trial_students_count,
      level,
    };

    const feedback = {
      valueUp,
      thumbUpTags,
      thumbDownTags,
    };

    yield* put(statisticsSuccess(statistics, feedback));
  } catch (e) {
    yield* put(stopLoading());
  }
}

function* statisticsSaga() {
  yield* takeEvery(GET_DASHBOARD_PENDING, loadStatistics);
}

function* loadEstimationTags() {
  try {
    const response = yield* call(fetchEstimationTags);
    const estimationTags = response?.data?._embedded?.estimation_tags || [];
    const tags = estimationTags
      .filter(({ is_archived }) => !is_archived)
      .reduce(
        (
          accumObject,
          {
            id,
            _embedded: {
              estimation_tag_strings = [{ locale_label: '', language: 'en' }],
            },
          },
        ) => {
          return {
            ...accumObject,
            [id]: estimation_tag_strings.find(({ language }) => language === 'en')?.locale_label || '',
          };
        },
        {},
      );
    yield* put(estimationTagsSuccess(tags));
  } catch (e) {
    yield* put(stopLoading());
  }
}

function* estimationTagsSaga() {
  yield* takeEvery(GET_DASHBOARD_PENDING, loadEstimationTags);
}

function* loadUpcomingClasses() {
  try {
    let response = yield* call(fetchUpcomingClasses, true);

    if (response.data._embedded?.classes?.length < 10) {
      response = yield* call(fetchUpcomingClasses);
    }

    const upcomingClasses = response?.data?._embedded?.classes || [];

    const nextClass = getNextClass(upcomingClasses);

    const normalizedUpcomingClasses = upcomingClasses.map(normalizeUpcomingClass);

    let normalizedNextClass: NextClassType | null = null;

    if (nextClass) {
      normalizedNextClass = normalizeNextClass(nextClass);
    }

    let filteredUpcomingClasses = normalizedUpcomingClasses;

    if (normalizedNextClass) {
      filteredUpcomingClasses = normalizedUpcomingClasses.filter(
        (item) => item?.id !== normalizedNextClass?.id,
      );
    }

    yield* put(upcomingClassesSuccess(filteredUpcomingClasses, normalizedNextClass));

    if (normalizedNextClass) {
      yield* all([
        put(nextClassFeedbackPending(normalizedNextClass.id)),
        put(nextClassCurriculumPending(normalizedNextClass.lessonId)),
      ]);
    }
  } catch (e) {
    yield* put(stopLoading());
  }
}

function* upcomingClassesSaga() {
  yield* takeEvery(GET_DASHBOARD_PENDING, loadUpcomingClasses);
}

function* loadCompletedClasses() {
  try {
    const response = yield* call(fetchCompletedClasses, 0);
    const completedClasses = response.data?._embedded?.classes || [];

    const normalizedCompletedClasses = completedClasses.map(normalizeCompletedClass);

    const nextCompletedClassesLink = response.data?._links?.next?.href || null;
    yield* put(completedClassesSuccess(normalizedCompletedClasses, nextCompletedClassesLink));
  } catch (e) {
    yield* put(stopLoading());
  }
}

function* completedClassesSaga() {
  yield* takeEvery(GET_DASHBOARD_PENDING, loadCompletedClasses);
}

function* loadMoreCompletedClasses(
  { payload: { nextLink } }: { type: string, payload: { nextLink: string } },
) {
  try {
    const response = yield* call(fetchMoreCompletedClasses, nextLink);
    const completedClasses = response.data?._embedded?.classes || [];

    const normalizedCompletedClasses = completedClasses.map(normalizeCompletedClass);

    const nextCompletedClassesLink = response.data?._links?.next?.href || null;
    yield* put(moreCompletedClassesSuccess(normalizedCompletedClasses, nextCompletedClassesLink));
  } catch (e) {
    yield* put(stopLoading());
  }
}

function* moreCompletedClassesSaga() {
  yield* takeEvery(GET_MORE_COMPLETED_CLASSES_PENDING, loadMoreCompletedClasses);
}

function* loadNextClassCurriculum(
  { payload: { lessonId } }: { type: string, payload: { lessonId: number } },
) {
  try {
    const response = yield* call(fetchCurriculumLesson, lessonId);
    const lessonTitle = response?.data?.full_name;

    yield* put(nextClassCurriculumSuccess(lessonTitle));
  } catch (e) {
    yield* put(stopLoading());
  }
}

function* nextClassCurriculumSaga() {
  yield* takeEvery(GET_NEXT_CLASS_CURRICULUM_PENDING, loadNextClassCurriculum);
}

type UpcomingClassCurriculumType = { type: string, payload: { classId: number, lessonId: number } };

function* loadUpcomingClassCurriculum(
  { payload: { classId, lessonId } }: UpcomingClassCurriculumType,
) {
  try {
    const response = yield* call(fetchCurriculumLesson, lessonId);
    const lessonTitle = response?.data?.full_name;
    yield* put(upcomingClassCurriculumSuccess(lessonTitle, classId));
  } catch (e) {
    yield* put(stopLoading());
  }
}

function* upcomingClassCurriculumSaga() {
  yield* takeEvery(GET_UPCOMING_CLASS_CURRICULUM_PENDING, loadUpcomingClassCurriculum);
}

function* loadRegionInfo(
  { payload: { regionCode } }: { type: string, payload: { regionCode: string } },
) {
  try {
    const response = yield* call(fetchRegion, regionCode);
    const region = response?.data || {};
    const {
      support_time_string,
      working_start_time,
      working_end_time,
      timezone,
      support_email,
      support_phone,
      support_phone2,
    } = region;

    const time = support_time_string || `${working_start_time}-${working_end_time}`;
    const period = region.working_start_time ? `${time} ${timezone}` : ('09:00-20:30 MSK');

    yield* put(regionSuccess({
      support: {
        mail: support_email,
        period,
        phone1: support_phone,
        phone2: support_phone2,
      },
    }));
  } catch (e) {
    yield* put(stopLoading());
  }
}

function* regionPendingSaga() {
  yield* takeEvery(GET_REGION_PENDING, loadRegionInfo);
}

function* loadUpcomingClassFeedback(
  { payload: { classId } }: { type: string, payload: { classId: number } },
) {
  try {
    const response = yield* call(fetchClassFeedback, classId);
    const feedback = response.data?.text || 'N/A';

    yield* put(upcomingClassFeedbackSuccess(feedback, classId));
  } catch (e) {
    yield* put(upcomingClassFeedbackSuccess('N/A', classId));
  }
}

function* upcomingClassFeedbackSaga() {
  yield* takeEvery(GET_UPCOMING_CLASS_FEEDBACK_PENDING, loadUpcomingClassFeedback);
}

function* loadNextClassFeedback(
  { payload: { classId } }: { type: string, payload: { classId: number } },
) {
  try {
    const response = yield* call(fetchClassFeedback, classId);
    const nextClassFeedback = response.data?.text || 'N/A';

    yield* put(nextClassFeedbackSuccess(nextClassFeedback));
  } catch (e) {
    yield* put(nextClassFeedbackSuccess('N/A'));
  }
}

function* nextClassFeedbackSaga() {
  yield* takeEvery(GET_NEXT_CLASS_FEEDBACK_PENDING, loadNextClassFeedback);
}

export default [
  upcomingClassCurriculumSaga(),
  upcomingClassFeedbackSaga(),
  moreCompletedClassesSaga(),
  nextClassCurriculumSaga(),
  nextClassFeedbackSaga(),
  completedClassesSaga(),
  upcomingClassesSaga(),
  estimationTagsSaga(),
  regionPendingSaga(),
  statisticsSaga(),
  teacherSaga(),
  userSaga(),
];
