import {
  RegionType,
} from 'types/common';

import {
  RegionSuccessAction,
  RegionPendingAction,
  ResetRegionActionType,
} from 'types/regionActionTypes';

import {
  GET_REGION_SUCCESS,
  GET_REGION_PENDING,
  RESET_REGION,
} from './actionTypes';

export const regionPending = (regionCode: string): RegionPendingAction => ({
  type: GET_REGION_PENDING,
  payload: { regionCode },
});

export const regionSuccess = (
  regionInfo: RegionType,
): RegionSuccessAction => ({
  type: GET_REGION_SUCCESS,
  payload: { regionInfo },
});

export const resetRegion = (): ResetRegionActionType => ({
  type: RESET_REGION,
});
