import { ProgramActionTypes } from 'types/programActionTypes';
import ProgramState, { ProgramUnitType, ProgramLessonType } from 'types/programType';

import {
  GET_PROGRAM_PENDING,
  PROGRAM_STOP_LOADING,
  GET_LESSON_SUCCESS,
  GET_LEVEL_SUCCESS,
  GET_UNIT_SUCCESS,
  RESET_PROGRAM,
} from './actionTypes';

const initialState: ProgramState = {
  levels: [],
  units: {},
  lessons: {},
  levelLoading: false,
  unitLoading: false,
  lessonLoading: false,
  topLoading: true,
};

export default (
  state = initialState,
  action: ProgramActionTypes,
): ProgramState => {
  switch (action.type) {
    case GET_PROGRAM_PENDING:
      return {
        ...state,
        levelLoading: true,
        unitLoading: true,
        lessonLoading: true,
        topLoading: false,
      };
    case PROGRAM_STOP_LOADING:
      return {
        ...state,
        levelLoading: false,
        unitLoading: false,
        lessonLoading: false,
        topLoading: false,
      };
    case GET_LEVEL_SUCCESS:
      return {
        ...state,
        levelLoading: false,
        levels: action.payload.levels.map((level) => ({
          id: level.id,
          name: level.name,
          number: level.number,
        })),
      };
    case GET_UNIT_SUCCESS:
      return {
        ...state,
        unitLoading: false,
        units: action.payload.units.reduce((accumData: ProgramUnitType, unit) => ({
          ...accumData,
          [unit.level_id]: [
            ...(accumData[unit.level_id] || []),
            { id: unit.id, name: `${unit.number}. ${unit.name}` },
          ],
        }), {}),
      };
    case GET_LESSON_SUCCESS:
      return {
        ...state,
        lessonLoading: false,
        lessons: action.payload.lessons
          .filter((lesson) => !lesson.is_trial)
          .reduce((accumData: ProgramLessonType, lesson) => ({
            ...accumData,
            [lesson.unit_id]: [
              ...(accumData[lesson.unit_id] || []),
              { id: lesson.id, name: lesson.name },
            ],
          }), {}),
      };
    case RESET_PROGRAM:
      return initialState;
    default: return state;
  }
};
