import {
  call,
  takeEvery,
  put,
} from 'typed-redux-saga';

import {
  getAuthToken,
  fetchUserInfo,
} from 'utils/request';

import { setToken } from 'utils/api';
import paths from 'routes/paths';

import { LoginRequestActionType } from 'types/authActionTypes';
import { loginSuccess, loginFail } from 'store/auth/actions';
import { userSuccess, resetUser } from 'store/user/actions';
import { resetDashboard } from 'store/dashboard/actions';
import { resetAccount } from 'store/account/actions';
import { resetLibrary } from 'store/library/actions';
import { resetProgram } from 'store/program/actions';
import { resetRegion } from 'store/region/actions';
import { resetSchedule } from 'store/schedule/actions';
import { resetTeacher } from 'store/teacher/actions';

import { USER_ID_KEY } from 'constants/common';
import history from 'utils/history';
import { resetAuth } from './actions';
import {
  LOGIN_REQUEST, LOGOUT_SUCCESS,
} from './actionTypes';

function* login({ payload }: LoginRequestActionType) {
  try {
    const { data } = yield* call(getAuthToken, payload.username, payload.password);

    setToken(data.value);

    localStorage.setItem(USER_ID_KEY, data.user_id.toString());

    yield* put(loginSuccess(data));
    const { data: user } = yield* call(fetchUserInfo, data.user_id);
    yield* put(userSuccess(user));

    history.push(paths.dashboard);
  } catch (e) {
    if (e.response?.data) { // eslint-disable-line
      yield* put(loginFail(e.response.data)); // eslint-disable-line
    }
  }
}

function* logout() {
  yield* put(resetAuth());
  yield* put(resetDashboard());
  yield* put(resetAccount());
  yield* put(resetLibrary());
  yield* put(resetProgram());
  yield* put(resetRegion());
  yield* put(resetSchedule());
  yield* put(resetTeacher());
  yield* put(resetUser());
}

function* loginSaga() {
  yield* takeEvery(LOGIN_REQUEST, login);
}

function* logoutSaga() {
  yield* takeEvery(LOGOUT_SUCCESS, logout);
}

export default [
  loginSaga(),
  logoutSaga(),
];
