import set from 'date-fns/set';
import add from 'date-fns/add';

import {
  ClassFeedbackType,
  ClassRequestParams,
  ClassResponseType,
  EstimationTagsType,
  HealthHistoryResponseType,
  ClassesHistoryResponseType,
  LessonType,
  LibraryLinksResponseType,
  LibrarySectionResponseType,
  PrivateStatisticType,
  RegionType,
  TeacherType,
  TimezonesResponseType,
  TransactionsResponseType,
  UpcomingClassesResponseType,
  UserType,
  LevelsResponseType,
  UnitsResponseType,
  LessonsResponseType,
  WeeklySlotsResponseType,
  OperationRequstParams,
  TeacherOperationResponseType,
  AuthTokenResponseType,
  ChangeUserInfoRequestParams,
} from 'types/common';
import store from 'store/createStore';

const getUserId = (): number => {
  return store?.getState()?.user?.info?.id ?? NaN;
};

export const fetchTeacherInfo = (teacherId?: number): Promise<AxiosResponse<TeacherType>> => {
  const id = getUserId() || teacherId || '';
  return api.get(`/teachers/${id}`);
};

export const getAuthToken = (
  login: string,
  password: string,
): Promise<AxiosResponse<AuthTokenResponseType>> => {
  return api.post('/auth/tokens', { login, password });
};

export const fetchUserInfo = (userId?: number): Promise<AxiosResponse<UserType>> => {
  const id = getUserId() || userId || '';
  return api.get(`/users/${id}`);
};

export const changeUserInfo = (
  data: ChangeUserInfoRequestParams,
): Promise<AxiosResponse<UserType>> => {
  return api.patch(`/users/${getUserId()}`, data);
};

