export const fetchUpcomingClasses = (
  duringTheWeek?: boolean,
): Promise<AxiosResponse<UpcomingClassesResponseType>> => {
  const start_time = set(
    sub(new Date(), { days: 1 }),
    {
      hours: 0, minutes: 0, seconds: 0, milliseconds: 0,
    },
  );

  const startTimeFormat = formatISO9075(start_time).replace(' ', 'T');

  const startTimeLt = formatISO9075(
    add(start_time, { days: 7 }),
  ).replace(' ', 'T');

  return api.get(`/users/${getUserId()}/classes`, {
    params: {
      limit: 10,
      offset: 0,
      order_by: 'start_time',
      ...(duringTheWeek && {
        start_time__gte: `${startTimeFormat}.000Z`,
        start_time__lt: `${startTimeLt}.000Z`,
      }),
      status: ['scheduled', 'ongoing'],
    },
  });
};

export const fetchCompletedClasses = (
  page: number,
  nextUrl?: string,
): Promise<AxiosResponse<ClassResponseType>> => {
  const url = nextUrl || `/users/${getUserId()}/classes`;

  return api.get(url, {
    params: {
      limit: 10,
      offset: page * 10,
      status: ['done', 'stopped'],
    },
  });
};

