export const fetchTeacherClasses = (
  params: ClassRequestParams = {},
): Promise<AxiosResponse<ClassResponseType>> => {
  const requestParams: ClassRequestParams = {
    limit: params.limit === undefined ? 1000 : params.limit,
    offset: params.offset === undefined ? 0 : params.offset,
    detailed: params.detailed === undefined ? true : params.detailed,
    compatibility_version: (
      params.compatibility_version === undefined
        ? 1
        : params.compatibility_version
    ),
    status: params.status === undefined ? [] : params.status,
  };

  if (params.start_time__gte) {
    requestParams.start_time__gte = params.start_time__gte;
  }

  if (params.start_time__lt) {
    requestParams.start_time__lt = params.start_time__lt;
  }

  return api.get(`/teachers/${getUserId()}/classes`, {
    params: requestParams,
  });
};

