export const teacherOperations = (
  params: OperationRequstParams = {},
): Promise<AxiosResponse<TeacherOperationResponseType>> => {
  const requestParams: OperationRequstParams = {
    action: params.action,
    start_time: params.start_time,
    teacher_id: getUserId(),
    stand_by: params.stand_by === undefined ? false : params.stand_by,
    weekly: params.weekly === undefined ? false : params.weekly,
  };

  return api.post(`/teachers/${getUserId()}/operations`, requestParams);
};
