import { LevelType, UnitType, LessonType } from 'types/common';
import {
  ProgramStopLoadingAction,
  ProgramPendingAction,
  LevelSuccessAction,
  UnitSuccessAction,
  LessonSuccessAction,
  LevelPendingAction,
  UnitPendingAction,
  LessonPendingAction,
  ResetProgramActionType,
} from 'types/programActionTypes';

import {
  GET_PROGRAM_PENDING,
  PROGRAM_STOP_LOADING,
  GET_LESSON_PENDING,
  GET_LESSON_SUCCESS,
  GET_LEVEL_PENDING,
  GET_LEVEL_SUCCESS,
  GET_UNIT_PENDING,
  GET_UNIT_SUCCESS,
  RESET_PROGRAM,
} from './actionTypes';

export const programPending = (): ProgramPendingAction => ({
  type: GET_PROGRAM_PENDING,
});

export const stopLoading = (): ProgramStopLoadingAction => ({
  type: PROGRAM_STOP_LOADING,
});

export const levelSuccess = (
  levels: LevelType[],
): LevelSuccessAction => ({
  type: GET_LEVEL_SUCCESS,
  payload: { levels },
});

export const unitSuccess = (
  units: UnitType[],
): UnitSuccessAction => ({
  type: GET_UNIT_SUCCESS,
  payload: { units },
});

export const lessonSuccess = (
  lessons: LessonType[],
): LessonSuccessAction => ({
  type: GET_LESSON_SUCCESS,
  payload: { lessons },
});

export const levelPending = (): LevelPendingAction => ({
  type: GET_LEVEL_PENDING,
});

export const unitPending = (): UnitPendingAction => ({
  type: GET_UNIT_PENDING,
});

export const lessonPending = (): LessonPendingAction => ({
  type: GET_LESSON_PENDING,
});

export const resetProgram = (): ResetProgramActionType => ({
  type: RESET_PROGRAM,
});
